import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
//弹窗
import pop from "@/components/ming-pop.vue";
//弹窗
import pop2 from "@/components/ming-pop2.vue";
export default {
	components:{
		pop,
		pop2
	},
	data() {
		return {
			user_info: '',
			data:[],
			id: 0,
			is_preview:false,
			
			//弹窗位置，默认上方
			direction: "up",
			
			//选择的订单索引index
			check_index:0,
			master_thumb:'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eodJHgGcKwtDiaWVkzIebbg4t1GrSCp0PDk6DblSkPbQSQ9EsX29G3p8pBB4hbEGibxiaX8jyKERtsCg/132',
			master_name:'王师傅',
			blue_star:5,
			hui_star:0,
			
			
		}
	},
	onLoad(options) {
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			if(!this.is_preview){
				this.initData();
			}
		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		//初始化订单详情
		initData(){
			this.request({
				url: interfaces.orderDetail,
				data: {
					id: this.id,
				},
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.is_preview=true;
						
						if(res.data.pics!=''){
							res.data.pics = res.data.pics.split(",");
						}else{
							res.data.pics=[];
						}
						
						res.data.create_time_txt = common.formatDT(res.data.create_time, 'yyyy.MM.dd h:m');
						res.data.status2_time_txt = common.formatDT(res.data.status2_time, 'yyyy.MM.dd h:m');
						res.data.status3_time_txt = common.formatDT(res.data.status3_time, 'yyyy.MM.dd h:m');
						res.data.status9_time_txt = common.formatDT(res.data.status9_time, 'yyyy.MM.dd h:m');
						this.data=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		//点击报修图片预览图片事件
		preImage(url) {
			wx.previewImage({
				current: url,
				urls: this.data.pics,
			})
		},
		//拨打师傅电话
		callPhone(n) {
			wx.makePhoneCall({
				phoneNumber: n
			});
		},
		//给师傅发信息
		message(){
			wx.showToast({
				title: '功能未开放，敬请期待',
				icon: "none",
			});
		},
		//报价下单选择维修师傅
		select(baoming_id){
			var _this = this;
			//一口价下单改为报价下单
			wx.showModal({
				title: '提示',
				content: '确认要选择该师傅维修吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderSelect,
							header: {
								token: _this.user_info.token
							},
							data: {
								order_id: _this.id,
								baoming_id:baoming_id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.initData();
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//普通客户取消订单
		cancel() {
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认要取消订单吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderCancel,
							header: {
								token: _this.user_info.token
							},
							data: {
								id: _this.data.id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.data.status=9;
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//取消的订单重新下单,订单到待审核状态
		recall(){
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认要重新下订单吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderRecall,
							header: {
								token: _this.user_info.token
							},
							data: {
								id: _this.data.id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.data.status = 0;
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//一口价改报价下单，报价下单改一口价订单
		change(){
			var _this = this;
			if(this.data.type==1){
				//一口价下单改为报价下单
				wx.showModal({
					title: '提示',
					content: '确认要改为报价下单吗？',
					success: function(res) {
						if (res.confirm) {
							_this.request({
								url: interfaces.orderChange,
								header: {
									token: _this.user_info.token
								},
								data: {
									id: _this.data.id,
									price:0
								},
								method: 'POST',
								success: res => {
									console.log(res);
									if (res.code == 200) {
										_this.data.type = 2;
										_this.data.total_price = 0;
									} else {
										app.isLogin(res.code, res.msg);
									}
								}
							})
						}
					}
				})
			}else{
				//报价下单改为一口价下单
				this.open('center');
			}
		},
		//报价下单修改价格
		editPrice(){
			this.open('center');
		},
		//跳转订单支付页
		goPay() {
			wx.navigateTo({
				url: 'pay?id=' + this.data.id,
			})
		},
		//评价
		evaluate(){
			this.direction = 'center';
			this.master_thumb=this.data.master.thumb;
			this.master_name=this.data.master.realname;
			this.blue_star=this.data.master.star;
			this.hui_star=5-this.data.master.star;
			//打开弹窗
			this.$refs.pop2.show();
		},
		//弹窗组件
		open(direction) {
			this.direction = direction;
			//打开弹窗
			this.$refs.pop.show();
		},
		//表单提交事件，报价下单改为一口价下单，或者一口价下单修改价格
		formSubmit1(e) {
			var data = e.detail.value;
			data.id=this.id;
			if (!data.price >0) {
				wx.showToast({
					title: '请输入正确的价格',
					icon: "none",
				});
				return;
			}
			this.request({
				url: interfaces.orderChange,
				data: data,
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						
						this.initData();
						this.$refs.pop.close();
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		//表单提交事件，报价下单改为一口价下单，或者一口价下单修改价格
		formSubmit2(e) {
		
			var data = e.detail.value;
			data.id=this.id;
			data.star=this.blue_star;
			this.request({
				url: interfaces.orderEval,
				data: data,
				header: {
					token: _this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
					
						this.initData();
						this.$refs.pop2.close();
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		checkStar(count,type){
			if(type==1){
				this.blue_star=count+1;
				this.hui_star=5-this.blue_star;
			}else{
				this.blue_star=this.blue_star+count+1;
				this.hui_star=5-this.blue_star;
			}
		},
	}
}
